import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import CreativeIcon from '@rsuite/icons/Creative';
import SearchBar from '../SearchBar/Searchbar';
import {Routes, Route, useNavigate} from 'react-router-dom';


export default function HomeAppBar() {
  const navigate = useNavigate();
  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static" sx={{ background: 'black' }}>
        <Toolbar>
          <IconButton  size="large" edge="start" color="inherit" aria-label="menu" sx={{ mr: 1 }}>
            <CreativeIcon spin color="#f5a623" />
          </IconButton>

          <h2 style={{ fontFamily: 'cursive', color: '#f5a623' }} >i-ELECTRON</h2>



          <Typography component="div" sx={{ flexGrow: 1 }}>
            <SearchBar />
          </Typography>

          <div>
            <Button color="inherit" onClick={()=>{navigate('/')}} > Home  </Button>
            <Button color="inherit"  > About  </Button>
            <Button color="inherit"  > Contact </Button>
            <Button color="inherit" onClick={()=>{navigate('/signup')}} > Register </Button>
            <Button color="inherit" onClick={()=>{navigate('/signin')}} > Login </Button>
          </div>
        </Toolbar>
      </AppBar>
    </Box >
  );
}
