import * as React from 'react';
import { TextField, Button, Snackbar } from '@mui/material';
import { Grow } from '@mui/material';
import Box from '@mui/material/Box';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Routes, Route, useNavigate } from 'react-router-dom';


export default function AssignmentInfo() {
  const navigate = useNavigate();

  const [checked, setChecked] = React.useState(true);
  return (

    <Box sx={{ display: 'flex', flexDirection: 'column', height: '83vh' }}>


      <h3>Assignments</h3>
      <Grow
        in={checked}
        style={{ transformOrigin: '0 0 0' }}
        {...(checked ? { timeout: 1000 } : {})}
      >


        <Accordion>

          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Typography>Task 1</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              Complete the task and upload here.. <br></br>
              <Button variant="outlined" type="submit" fullWidth onClick={() => { navigate('/studentdashboard') }}
                style={{ background: 'black', color: 'white', marginTop: '2vh' }}
              >
                Upload
              </Button>
            </Typography>
          </AccordionDetails>
        </Accordion>

      </Grow>

    </Box>
  );
}


